@font-face {
  font-family: 'Source Sans Pro';
  src: url('./fonts/source-sans-pro/SourceSansPro-Regular.otf') format('opentype');
  font-weight: normal;
  font-style: normal;
  line-height: 1.5;
}

body {
  background-color: #2a2a2e;
  margin: 0;
  min-height: 100vh;
  display: grid;
  place-items: center;
}

.bodyContent {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.error-message {
  height: 40px;
  text-align: center;
  font-family: "Source Sans Pro";
  margin-bottom: 15px;
}

.no-select {
  -webkit-user-select: none;
  /* Safari */
  -ms-user-select: none;
  /* IE 10 and IE 11 */
  user-select: none;
  /* Standard syntax */
}

.loader {
  width: 48px;
  height: 48px;
  display: inline-block;
  position: relative;
  color: #FFF;
  -webkit-animation: rotation 1s linear infinite;
  animation: rotation 1s linear infinite;
}

.loader:after,
.loader:before {
  content: "";
  position: absolute;
  width: 24px;
  height: 24px;
  top: 50%;
  left: 50%;
  transform: scale(0.5) translate(0, 0);
  background-color: #38383d;
  border-radius: 50%;
  -webkit-animation: animloader38 1s infinite ease-in-out;
  animation: animloader38 1s infinite ease-in-out;
}

.loader:before {
  background-color: #ff5c50;
  transform: scale(0.5) translate(-48px, -48px);
}

#recorder {
  position: relative;
  width: 4rem;
  margin: 20px 0 15px 0;
  height: 3rem;
  border-radius: 3rem;
  background: #38383d;
  border: 1px solid #f9f9fa33;
  cursor: pointer;
  box-shadow: 0 1px 4px 0 rgba(12, 12, 13, 0.2), 0 0 0 1px rgba(0, 0, 0, 0.15);
  transition: 0.2s ease;
}

#recorder #record {
  width: 60%;
  height: 60%;
  top: 20%;
  left: 20%;
  position: absolute;
  transition: inherit;
}

#recorder #arrow {
  width: 50%;
  height: 50%;
  top: 30%;
  left: 25%;
  position: absolute;
  transition: inherit;
  opacity: 0;
}

#recorder:active {
  border-color: transparent;
}

#recorder:active #record {
  width: 55%;
  height: 55%;
  top: 23%;
  left: 23%;
}

#recorder.recording #record {
  animation: recording 0.7s ease infinite;
}

#recorder.download #record {
  height: 40%;
  width: 40%;
  top: 15%;
  left: 30%;
  animation: none;
}

#recorder.download #arrow {
  animation: download 0.5s ease infinite;
}

#recorder.out #record {
  animation: out 0.8s ease, in 0.2s 0.8s ease;
}

#recorder.disabled {
  opacity: 50%;
}

#recorder.disabled:active #record {
  width: 60%;
  height: 60%;
  top: 20%;
  left: 20%;
}

@-webkit-keyframes animloader38 {
  50% {
    transform: scale(1) translate(-50%, -50%);
  }
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

@keyframes animloader38 {
  50% {
    transform: scale(1) translate(-50%, -50%);
  }
}

@keyframes in {
  from {
    height: 0%;
    top: 60%;
  }
}

@keyframes recording {

  from,
  to {
    transform: rotate(10deg);
  }

  50% {
    transform: rotate(-10deg);
  }
}

@keyframes download {
  0% {
    top: 30%;
    opacity: 0;
  }

  50% {
    opacity: 1;
  }

  100% {
    top: 55%;
    opacity: 0;
  }
}

@keyframes out {
  0% {
    top: 15%;
    height: 40%;
  }

  20% {
    top: 8%;
  }

  75%,
  100% {
    top: 100%;
    opacity: 0;
    height: 0px;
  }
}